export default {
  name: "testPage",
  data() {
    return {
      workbenchList: []
    };
  },
  methods: {
    async getData() {
      this.$utils.showLoading("正在加载");
      this.$ajax
        .post("kpapi/evafbappeal/search", {
          currentPage: 1,
          pageSize: 2,
          mappingSql: "EvaFbAppeal.List",
          orderby: "ModifiedOn desc",
          searchDict: JSON.stringify({})
        })
        .then(resp => {
          if (resp.ErrorCode == 0) {
            console.log(resp);
          }
        });
    }
  },
  mounted() {
    this.getData();
  }
};



